import { Routes, RouterModule, Router } from "@angular/router";
//Layouts
import {
  CondensedComponent,
  ThyTeknikComponent,
  BlankComponent,
} from "./@pages/layouts";

//Sample Pages
import { CondensedDashboardComponent } from "./dashboard/condensed/dashboard.component";
import { ThyTeknikDashboardComponent } from "./dashboard/thyteknik/dashboard.component";
import { CardsComponentPage } from "./cards/cards.component";
import { ViewsPageComponent } from "./views/views.component";
import { ChartsComponent } from "./charts/charts.component";
import { SocialComponent } from "./social/social.component";
import { NgModule } from "@angular/core";

const _pagesMainRoutes: Routes = [
  {
    path: "",
    data: {
      breadcrumb: "Home"
    },
    component: CondensedComponent
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "dashboard",
        component: CondensedDashboardComponent
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "extra",
        loadChildren: "./extra/extra.module#ExtraModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "layouts",
        loadChildren: "./layouts/layouts.module#LayoutPageModule"
      }
    ]
  },
  {
    path: "condensed",
    component: BlankComponent,
    children: [
      {
        path: "session",
        loadChildren: "./session/session.module#SessionModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "forms",
        loadChildren: "./forms/forms.module#FormsPageModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "builder",
        loadChildren: "./builder/builder.module#BuilderModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "ui",
        loadChildren: "./ui/ui.module#UiModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "email",
        loadChildren: "./email/email.module#EmailModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "social",
        component: SocialComponent
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "cards",
        component: CardsComponentPage
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "views",
        component: ViewsPageComponent
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "maps",
        loadChildren: "./maps/maps.module#MapsModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "tables",
        loadChildren: "./tables/tables.module#TablesModule"
      }
    ]
  },
  {
    path: "condensed",
    component: CondensedComponent,
    children: [
      {
        path: "charts",
        component: ChartsComponent
      }
    ]
  },
  //ThyTeknik
  {
    path: "",
    data: {
      breadcrumb: "Home"
    },
    component: ThyTeknikComponent
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "dashboard",
        component: ThyTeknikDashboardComponent
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "extra",
        loadChildren: "./extra/extra.module#ExtraModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "layouts",
        loadChildren: "./layouts/layouts.module#LayoutPageModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: BlankComponent,
    children: [
      {
        path: "session",
        loadChildren: "./session/session.module#SessionModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "forms",
        loadChildren: "./forms/forms.module#FormsPageModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "builder",
        loadChildren: "./builder/builder.module#BuilderModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "ui",
        loadChildren: "./ui/ui.module#UiModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "email",
        loadChildren: "./email/email.module#EmailModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "social",
        component: SocialComponent
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "cards",
        component: CardsComponentPage
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "views",
        component: ViewsPageComponent
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "maps",
        loadChildren: "./maps/maps.module#MapsModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "tables",
        loadChildren: "./tables/tables.module#TablesModule"
      }
    ]
  },
  {
    path: "thyteknik",
    component: ThyTeknikComponent,
    children: [
      {
        path: "charts",
        component: ChartsComponent
      }
    ]
  },

];

@NgModule({
  imports: [
    RouterModule.forChild(_pagesMainRoutes)
  ],
  exports: [RouterModule]
})

export class PagesRoutingModule { }
